<template>
  <div>
    <Back :title="$t('AddVolume_createDataVolume')"></Back>
    <el-form :model="form" ref="ruleForm" label-width="130px" class="demo-ruleForm" style="padding: 10px 10px 1px 10px; margin-top: 20px">
      <el-form-item :label=" $t('AddVolume_dataVolumeName')" prop="name">
        <el-input v-model="form.name" :placeholder="$t('AddVolume_plsInputDataVolumeName')"></el-input>
      </el-form-item>
    </el-form>
    <el-form :model="form" ref="ruleForm" label-width="50px" class="demo-ruleForm" style="padding: 10px 10px 1px 10px">
      <el-form-item label="">
        <el-button style="background: #067FB5; color: #fff;" size="small" @click="onSubmit"> {{ $t('AddVolume_createNow')}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Vue from 'vue'
import { Form, FormItem, Input, Button, Switch, Select, Option } from 'element-ui'
import post from '@/utils/post'
import Back from '@/components/Back'
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Button)
Vue.use(Switch)
Vue.use(Select)
Vue.use(Option)

export default {
  data () {
    return {
      systeminfo: null, // 系统数据
      form: {
        name: '', // 数据卷名
        Driver: 'local', // 驱动
        options: [], // 驱动选项
        isNFS: false // 是否为NFS卷，功能待完善
      },
      portNum: [], // 驱动选项匹配的数据。用于准确删除，添加驱动选项
      i: 0 // 标识
    }
  },
  mounted () {
    // 请求关于系统的数据
    const docker = 'curl --unix-socket /var/run/docker.sock -s "http:/v1.40/info"'
    post({ docker }, (res) => {
      this.systeminfo = res
    }, this)
  },
  methods: {
    // 添加开options的input框
    addPort () {
      this.portNum.push(this.i)
      this.form.options.push({ name: '', value: '' })
      this.i++
    },
    // 删除options的input框
    delPort (item, index) {
      this.portNum = this.portNum.filter(data => {
        return data !== item
      })
      this.form.options.splice(index, 1)
    },
    // 处理创建
    onSubmit () {
      let DriverOpts = {}
      if (this.form.options.length > 0) {
        for (let a = 0; a < this.form.options.length; a++) {
          DriverOpts[this.form.options[a].name] = this.form.options[a].value
        }
      }
      let data = {
        Name: this.form.name,
        Driver: this.form.Driver,
        DriverOpts
      }
      const docker = `curl --unix-socket /var/run/docker.sock -s -H Content-Type:application/json -d '${JSON.stringify(data)}' http:/v1.40/volumes/create` // 创建数据卷
      post({ docker }, res => {
        if (res.message) {
          return this.$message({
            showClose: true,
            message: res.message,
            type: 'error'
          })
        }
        this.$message({
          showClose: true,
          message:   this.$t('AddVolume_createDataVolumeSuccess'),
          type: 'success'
        })
        this.$router.go(-1)
      }, this)
    }
  },
  components: {
    Back
  }
}
</script>

